import { createContext, useEffect, useState } from "react";
import Axios from '../Axios';


export const NotificationContext = createContext()

const NotificationProvider = ({children}) => {
  const [loading , setLoading] = useState(false)
  const [notification  , setNotification] = useState([])
    const [notificationIcon  , setNotificationIcon] = useState(null)
    const fetchNotification = async()=>{
        try {
          setLoading(true)
          const {data:{notification}} = await Axios.get("/notification")
          setLoading(false)
          setNotification(notification.reverse())
          const checkNotification = localStorage.getItem("length")
          if(!checkNotification){
            localStorage.setItem("length",notification.length)
            return;
          }
          if(notification.length < checkNotification){

            localStorage.setItem("length","0")
          }else{
            const badge = notification.length - checkNotification
            setNotificationIcon(badge)
            localStorage.setItem("length",notification.length)
          }
        } catch (error) {
          setLoading(false)
        }
    }
    
    useEffect(()=>{
        fetchNotification()
    },[])
  return (
    <NotificationContext.Provider value={{notificationIcon, setNotificationIcon,notification,loading}}>
        {children}
    </NotificationContext.Provider>
  )
}

export default NotificationProvider