import { Avatar, Box, Button, Divider, Grid, Typography } from '@mui/material'
import React, { useEffect, useState } from 'react'
import Appbar from '../components/Appbar'
import ListContainer from '../components/List';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import SearchIcon from '@mui/icons-material/Search';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { styled, alpha } from '@mui/material/styles';
import Menu from '@mui/material/Menu';
import { Alert, Snackbar } from '@mui/material';
import { Link } from 'react-router-dom';
import InputBase from '@mui/material/InputBase';
import CardContent from '@mui/material/CardContent';
import MenuItem from '@mui/material/MenuItem';
import Axios from '../Axios';
import { Delete } from '@mui/icons-material';
import Loading from '../components/Loading';
import Refuse from '../components/Refuse';
import NoList from '../components/NoList';


const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
        marginLeft: theme.spacing(3),
        width: 'auto',
    },
}));


const SearchIconWrapper = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
}));


const StyledInputBase = styled(InputBase)(({ theme }) => ({
    color: 'inherit',
    '& .MuiInputBase-input': {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(6)})`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '50ch',
        },
    },
}));



const StyledMenu = styled((props) => (
    <Menu
        elevation={0}
        anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'right',
        }}
        transformOrigin={{
            vertical: 'top',
            horizontal: 'right',
        }}
        {...props}
    />
))(({ theme }) => ({
    '& .MuiPaper-root': {
        borderRadius: 6,
        marginTop: theme.spacing(1),
        minWidth: 180,
        color:
            theme.palette.mode === 'light' ? 'rgb(55, 65, 81)' : theme.palette.grey[300],
        boxShadow:
            'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
        '& .MuiMenu-list': {
            padding: '4px 0',
        },
        '& .MuiMenuItem-root': {
            '& .MuiSvgIcon-root': {
                fontSize: 18,
                color: theme.palette.text.secondary,
                marginRight: theme.spacing(1.5),
            },
            '&:active': {
                backgroundColor: alpha(
                    theme.palette.primary.main,
                    theme.palette.action.selectedOpacity,
                ),
            },
        },
    },
}));


const MyAcount = () => {
    const [loading, setLoading] = useState(false)
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [search, setSearch] = useState("")
    const [opens, setOpens] = useState(false)
    const [message, setMessage] = useState(null)
    const open = Boolean(anchorEl);
    const [videos, setVideos] = useState([])
    const [myDetails, setMyDetails] = useState({})
    const fetchVideo = async () => {
        try {
            setLoading(true)
            const { data } = await Axios.get("/myvideos")
            setLoading(false)
            setMyDetails(data.user)
            setVideos(data.show)
        } catch (error) {
            setLoading(false)
        }
    }

    const deleteHandler = async (id) => {
        try {
            setAnchorEl(null);
            const { data } = await Axios.delete(`/deletemyvideo/${id}`)
            setMessage(data)
            setOpens(true)
            fetchVideo()
        } catch (error) {
            setMessage(error.response.data)
            setOpens(true)
        }
    }

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    useEffect(() => {
        fetchVideo()
    }, [])

    const filter = videos.filter(elm => {
        return elm.title.toLowerCase().includes(search.toString().toLocaleLowerCase())
    })


    return (

        <>
            <Appbar />
            <Grid container>
                <Grid item xs={2} sx={{ display: { xs: "none", md: "block" } }}>
                    <Box sx={{ width: "16.5vw", borderRight: "1px solid lightGray", height: "90vh", position: "fixed" }}>
                        <ListContainer />
                    </Box>
                </Grid>
                <Grid item xs={12} md={10}>
                    {loading ? <Loading /> : <Box sx={{
                        marginTop: "70px", width: "100%", height: "100%"
                        , gap: 3, flexDirection: "column", display: "flex", justifyContent: "center", alignItems: "center"
                    }}>
                        <Avatar src={myDetails.image ? `${myDetails.image}` : ""}
                            sx={{ width: "5rem", height: "5rem" }} />
                        <Box textAlign={"center"} sx={{ width: "100%" }}>
                            <Typography variant='h4' sx={{ fontWeight: "500", marginBottom: "10px", color: "#ccc" }}>{myDetails.name}</Typography>
                            <Typography variant='h5' sx={{ fontWeight: "500", marginBottom: "10px", color: "#ccc" }}>{myDetails.email}</Typography>
                            <Button variant='outlined' sx={{ margin: "10px 0" }}>subcribers</Button>
                            <Typography variant='outlined' sx={{ margin: "10px 0px 10px 10px", color: "#ccc" }}>{myDetails.subcriber && myDetails.subcriber.length}</Typography>
                            <Divider sx={{ bgcolor: "#ccc" }} />
                        </Box>
                        <Box>
                            <Box sx={{ margin: "10px auto",width:"80%", display:"grid",placeItems:"center" }}>
                                <Search>
                                    <SearchIconWrapper>
                                        <SearchIcon sx={{ color: "#ccc" }} />
                                    </SearchIconWrapper>
                                    <StyledInputBase
                                        placeholder="Search"
                                        value={search}
                                        sx={{ color: "#ccc" }}
                                        onChange={(e) => setSearch(e.target.value)}
                                        inputProps={{ 'aria-label': 'search' }}
                                    />
                                </Search>
                            </Box>
                            <Grid container display={"flex"} gap={3} justifyContent={"center"} alignItems={"center"}>
                                {
                                    filter.map((video, ind) => {
                                        return (<Grid item key={ind}>
                                            <Card sx={{ maxWidth: 345, maxHeight: 320, bgcolor: "black", boxShadow: `2px 1px 5px 1px #ccc` }}>
                                                <Box className={'ListimageContainer'} sx={{ width: { xs: "345px", sm: "345px" }, height: { xs: "22vh", sm: "30vh" } }}><video
                                                    preload='auto'
                                                    muted
                                                    src={`${video.video}`} /></Box>
                                                <CardContent>
                                                    <Link to={`/dashboad/videos/${video._id}`} style={{ display: "flex", columnGap: "10px", alignItems: "center", height: "50px" }}>
                                                        <Avatar src={myDetails.image ? `${myDetails.image}` : ""} />
                                                        <Typography variant="h5" component="div" sx={{ color: "#ccc", width: "250px" }}>
                                                            {video.title.length > 48 ? `${video.title.slice(0, 48)}...` : video.title}
                                                        </Typography>
                                                    </Link>
                                                </CardContent>
                                                <CardActions sx={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
                                                    <Typography variant="body2" color="text.secondary" sx={{ color: "#ccc" }}>{video.veiws && video.veiws.length} veiw's </Typography>
                                                    <MoreVertIcon onClick={handleClick} sx={{ color: "#ccc", cursor: "pointer" }} />
                                                    <StyledMenu
                                                        id="demo-customized-menu"
                                                        MenuListProps={{
                                                            'aria-labelledby': 'demo-customized-button',
                                                        }}

                                                        anchorEl={anchorEl}
                                                        open={open}
                                                        onClose={handleClose}
                                                    >
                                                        <MenuItem disableRipple onClick={() => deleteHandler(video._id)}>
                                                            <Delete />
                                                            <Typography >Remove</Typography>
                                                        </MenuItem>
                                                    </StyledMenu>
                                                </CardActions>
                                            </Card>

                                        </Grid>)
                                    })
                                }
                            </Grid>
                        </Box>
                    </Box>}
                    {
                        (!loading && message) && <Refuse />
                    }
                    {
                        (filter.length===0 && !loading) &&<NoList />
                    }
                </Grid>
            </Grid>
            {
                message && <Snackbar autoHideDuration={3000} open={opens} onClose={() => setOpens(false)}>
                    <Alert onClose={() => setOpens(false)} variant='filled' severity={message.success ? "success" : "error"}>
                        {message.message}
                    </Alert>
                </Snackbar>
            }
        </>
    )
}

export default MyAcount