import * as React from 'react';
import { styled, alpha } from '@mui/material/styles';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import logo from '../assets/background/logo.jpg'
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import InputBase from '@mui/material/InputBase';
import MenuItem from '@mui/material/MenuItem';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import SearchIcon from '@mui/icons-material/Search';
import MoreIcon from '@mui/icons-material/MoreVert';
import { Alert, Avatar, Divider, Snackbar } from '@mui/material';
import { DataContext } from '../context/drawer';
import { SearchContext } from '../context/search';
import Axios from '../Axios';
import { Link } from 'react-router-dom';

const Search = styled('div')(({ theme }) => ({
  position: 'relative',
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  '&:hover': {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginRight: theme.spacing(2),
  marginLeft: 0,
  width: '100%',
  [theme.breakpoints.up('sm')]: {
    marginLeft: theme.spacing(3),
    width: 'auto',
  },
}));

const SearchIconWrapper = styled('div')(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: '100%',
  position: 'absolute',
  pointerEvents: 'none',
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'center',
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: 'inherit',
  '& .MuiInputBase-input': {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(6)})`,
    transition: theme.transitions.create('width'),
    width: '100%',
    [theme.breakpoints.up('md')]: {
      width: '50ch',
    },
  },
}));

const Appbar = () => {
  const [message, setMessage] = React.useState({})
  const [open, setOpen] = React.useState(false)
  const { search, setSearch, myaccount } = React.useContext(SearchContext)
  const { setDrawerOpen } = React.useContext(DataContext)
  const [anchorEl, setAnchorEl] = React.useState(null);

  const isMenuOpen = Boolean(anchorEl);

  const handleProfileMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };



  const handleMenuClose = () => {
    setAnchorEl(null);
  };


  const logOutHandler = async () => {
    try {
      const { data } = await Axios.put("/log-out")
      setMessage(data)
      setOpen(true)
      window.location.replace('/login')
    } catch (error) {
      setMessage(error.response.data)
      setOpen(true)
    }
  }

  const menuId = 'primary-search-account-menu';
  const renderMenu = (
    <Menu
      anchorEl={anchorEl}
      anchorOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      id={menuId}
      keepMounted
      transformOrigin={{
        vertical: 'top',
        horizontal: 'right',
      }}
      PaperProps={{
        sx: { bgcolor: "black", color: "#ccc", boxShadow: "1px 1px 5px 1px #ccc" }
      }}
      open={isMenuOpen}
      onClose={handleMenuClose}
    >
      <MenuItem onClick={handleMenuClose}>
        <Avatar
          src={myaccount.image ? `${myaccount.image}` : ""} />
        <Box marginLeft={2}>
          <Typography>{myaccount.name}</Typography>
          <Typography>{myaccount.email}</Typography>
        </Box>
      </MenuItem>
      <Divider sx={{ bgcolor: "#ccc" }} />
      <MenuItem onClick={handleMenuClose}><Link to={"/dashboad/myacount"} style={{ color: "#ccc" }}>My account</Link></MenuItem>
      <MenuItem onClick={logOutHandler}>Log Out</MenuItem>
    </Menu>
  );


  return (
    <>
      <Box sx={{ flexGrow: 1 }}>
        <AppBar sx={{ position: "fixed" }} style={{ background: "inherit", backdropFilter: "blur(5px)" }}>
          <Toolbar >
            <Box sx={{ padding: "0 25px 0 5px" }} onClick={() => setDrawerOpen(true)}>
              <MenuIcon sx={{ display: { xs: 'block', md: 'none' } }} /></Box>
            <Box
              variant="h6"
              component="div"
              sx={{ display: { xs: 'none', md: 'block' } }}
            >
              <img src={logo} alt="logo" style={{ width: "50px", height: "50px", objectFit: "cover" }} />
            </Box>
            <Box >
              <Search>
                <SearchIconWrapper>
                  <SearchIcon />
                </SearchIconWrapper>
                <StyledInputBase
                  placeholder="Search"
                  value={search}
                  onChange={(e) => setSearch(e.target.value)}
                  inputProps={{ 'aria-label': 'search' }}
                />
              </Search>
            </Box>
            <Box sx={{ flexGrow: 1 }} />
            <Box sx={{ display: { xs: 'none', md: 'flex' } }}>
              <IconButton
                size="large"
                edge="end"
                aria-label="account of current user"
                aria-controls={menuId}
                aria-haspopup="true"
                onClick={handleProfileMenuOpen}
                color="inherit"
              >
                <Avatar
                  src={myaccount.image ? `${myaccount.image}` : ""} />
              </IconButton>
            </Box>
            <Box sx={{ display: { xs: 'flex', md: 'none' } }}>
              <IconButton
                size="large"
                edge="end"
                aria-label="account of current user"
                aria-controls={menuId}
                aria-haspopup="true"
                onClick={handleProfileMenuOpen}
                color="inherit"
              >
                <MoreIcon />
              </IconButton>
            </Box>
          </Toolbar>
        </AppBar>
        {renderMenu}
      </Box>
      <Snackbar onClose={() => setOpen(false)} open={open} autoHideDuration={3000}>
        <Alert onClose={() => setOpen(false)} variant='filled' severity={message.success ? "success" : "error"}>
          {message.message}
        </Alert>
      </Snackbar>
    </>
  );
}


export default Appbar