import { Alert, Box, Button, CircularProgress, Input, Snackbar, Typography } from '@mui/material'
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import background1 from '../assets/background/background.jpg'

import Axios from '../Axios';
import Cookies from 'universal-cookie';


const Login = () => {

    const cookie = new Cookies()
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [confirmPassword, setConfirmPassword] = useState('')
    const [message, setMessage] = useState({})
    const [open, setOpen] = useState(false)
    const [loading, setLoading] = useState(false)

    const submitHandler = async (e) => {
        e.preventDefault()
        try {
            setLoading(true)
            const { data } = await Axios.post("/login", {
                email,
                password,
                confirmPassword
            })
            setMessage(data)
            cookie.set("token", data.token, { path: "/dashboad" })
            setOpen(true)
            window.location.replace("/dashboad/home")
        } catch (error) {
            setLoading(false)
            setMessage(error.response.data)
            setOpen(true)
        }
    }
    return (
        <>
            <Box sx={{ position: 'fixed', width: "100%", height: "100vh", zIndex: "1", opacity: "50%" }}>
                <img style={{ position: "absolute", width: "100%", height: "100%", objectFit: "cover" }}
                    src={background1} alt={"banner"} />
            </Box>
            <Box className={"design"}></Box>
            <Box component={"form"} onSubmit={submitHandler}
                width={"100%"} height={"100vh"} display={"flex"} justifyContent={"center"} alignItems={"center"}>
                <Box display={"flex"} width={"300px"}
                    height={"60vh"} flexDirection={"column"} justifyContent={"center"} alignItems={"center"}
                    className={"loginContainer"} rowGap={2.5}>
                    <Typography sx={{ color: "#ccc", width: "100%", paddingLeft: "30px" }} variant='h4'>Sign In</Typography>
                    <Input type='email'
                        sx={{ width: "78%", marginTop: "10px", color: "#ccc" }}
                        value={email}
                        onChange={(e) => setEmail(e.target.value)}
                        placeholder='Enter Your Email' />
                    <Input type='password'
                        sx={{ width: "78%", color: "#ccc" }}
                        value={password}
                        onChange={(e) => setPassword(e.target.value)}
                        placeholder='Enter Your Password' />
                    <Input type='password'
                        sx={{ width: "78%", color: "#ccc" }}
                        value={confirmPassword}
                        onChange={(e) => setConfirmPassword(e.target.value)}
                        placeholder='Enter Your ConfirmPassword' />
                    <Button type='submit' sx={{ width: "80%" }} variant={"contained"}>{!loading ? "SIGN IN" : <CircularProgress style={{ color: "#ccc", width: "25px", height: "25px" }} />}</Button>
                    <Typography sx={{ color: "rgb(190, 188, 188)", fontSize: "0.8rem" }}>You have not an Account ?
                        <Link to={'/sign-up'} style={{ paddingLeft: "10px", color: "#fc2b2b", fontSize: "0.9rem" }}>SignUp...</Link>
                    </Typography>
                </Box>
            </Box>
            <Snackbar autoHideDuration={3000} open={open} onClose={() => setOpen(false)}>
                <Alert onClose={() => setOpen(false)} variant='filled' severity={message.success ? "success" : "error"}>
                    {message.message}
                </Alert>
            </Snackbar>
        </>
    )
}

export default Login