import { Box, Button, Typography } from '@mui/material';
import error from '../assets/background/background.jpg'
import React from 'react';
import { Link } from 'react-router-dom';

const Error = () => {
    return (
        <>
            <Box sx={{ width: "100%", height: "100vh", display: "grid", placeItems: "center", position: "relative", bgcolor: "black" }}>
                <img src={error} alt="error page" style={{ width: "100%", height: "100%", position: "absolute", opacity: "50%" }} />
                <Box sx={{ width: { xs: "80%", sm: "400px", display: "grid", placeItems: "center" } }}>
                    <Typography variant='h1' sx={{ color: "#ccc" }}>404</Typography>
                    <Typography variant='h3' sx={{ color: "#ccc", fontSize: { xs: "30px", sm: "45px" } }}>Page Not Found!</Typography>
                    <Button sx={{ width: { xs: "80%", sm: "100%" }, margin: "20px auto 0 auto" }} variant='contained'><Link to={'/dashboad/home'} style={{ color: "#ccc" }}>Go to home</Link></Button>
                </Box>
            </Box>
        </>
    )
}


export default Error
