import { Alert, Avatar, Box, Button, Divider, Grid, Input, ListItem, Snackbar, Typography } from '@mui/material'
import React, { useEffect, useState } from 'react';
import Appbar from '../components/Appbar';
import { Link, useParams } from 'react-router-dom';
import { ThumbDownAltOutlined, ThumbDownRounded, ThumbUpAltOutlined } from "@mui/icons-material";
import ThumbUpRoundedIcon from '@mui/icons-material/ThumbUpRounded';
import Axios from '../Axios';
import { io } from 'socket.io-client'
import Cookie from 'universal-cookie';
import Loading from '../components/Loading';
import Refuse from '../components/Refuse';
import { baseURL } from '../Axios';

const cookie = new Cookie()
const token = cookie.get("token")
const socket = io(baseURL, {
    auth: { token: token }
})


const PlayVideo = () => {
    const [comments, setComments] = useState([])
    const [loading, setLoading] = useState(false)
    const [typing, setTyping] = useState({ name: "", profile: "", typing: false, videoId: "" })
    const [likes, setLikes] = useState(null)
    const [comment, setComment] = useState("")
    const [unlikes, setUnLikes] = useState(null)
    const [owner, setOwner] = useState({})
    const [video, setVideo] = useState([])
    const [like, setLike] = useState(null)
    const [unlike, setUnlike] = useState(null)
    const [message, setMessage] = useState({})
    const [error, setError] = useState(null)
    const [open, setOpen] = useState(false)
    const [subcribe, setSubcribe] = useState(null)
    const { id } = useParams()
    const fetchvideo = async (id) => {
        try {
            setLoading(true)
            const { data } = await Axios.get(`/video/${id}`)
            setLoading(false)
            setComments(data.comments.reverse())
            setLike(data.like)
            setVideo(data.video)
            setLikes(data.video.like.length)
            setUnLikes(data.video.unlike.length)
            setUnlike(data.unlike)
            const res = await Axios.get(`/getvideoowner/${data.video.OwnerId}`)
            setSubcribe(res.data.subcribe)
            setOwner(res.data.owner)
        } catch (error) {
            setLoading(false)
            setMessage(error.response.data)
            setError(error.message)
        }
    }
    useEffect(() => {
        socket.on("connect", () => {
        })
        socket.on("disconnect", () => {
        })

        fetchvideo(id)
        return () => {
            socket.off("connect")
            socket.off("disconnect")
        }
    }, [id])

    const subcriberHandler = async (e) => {
        e.preventDefault()
        try {
            const { data } = await Axios.post(`/subcribe/${id}`, { id: video.OwnerId })
            data.message === "subcribed" ? setSubcribe(true) : setSubcribe(false)
            setMessage(data)
            setOpen(true)
            window.location.reload()
        } catch (error) {
            setMessage(error.response.data)
            setOpen(true)
            setError(error.message)
        }
    }

    const likeHandler = async () => {
        try {
            const { data } = await Axios.post('/likevideo', { id })
            setLike(data.like)
            if (data.like) {
                setLike(true)
            } else {
                setLike(false)
            }
            setMessage(data)
            setOpen(true)
            window.location.reload()
        } catch (error) {
            setMessage(error.response.data)
            setOpen(true)
            setError(error.message)
        }
    }

    useEffect(() => {
        socket.on("comments", ({ comm }) => {
            setComments(pre => [comm, ...pre])
        })
    }, [])

    const UnlikeHandler = async () => {
        try {
            const { data } = await Axios.post('/unlikevideo', { id })
            if (data.unlike) {
                setUnlike(true)
            } else {
                setUnlike(false)
            }
            setMessage(data)
            setOpen(true)
            window.location.reload()
        } catch (error) {
            setMessage(error.response.data)
            setOpen(true)
            setError(error.message)
        }
    }

    const inputHandler = (e) => {
        setComment(e.target.value)
        if (e.target.value === '') {
            socket.emit("typing", { name: "", profile: "", videoId: "", typing: false })
        } else {
            socket.emit("typing", { name: owner.name, profile: owner.image, videoId: video._id, typing: true })
        }
    }
    const addCommentHandler = async (e) => {
        e.preventDefault()
        try {
            socket.emit("addcomment", { comment, videoId: video._id })
            setComment("")
            socket.on("comment", ({ comments }) => {
                setComments((pre) => [comments, ...pre])
            })
            socket.emit("typing", { name: "", profile: "", videoId: "", typing: false })

        } catch (error) {
        }
    }

    socket.on("showtyping", ({ name, profile, videoId }) => {
        setTyping({ name: name, profile: profile, typing: true, videoId: videoId })
    })
    return (
        <>
            <Appbar />
            {loading ? <Loading /> : <Grid container marginTop={'60px'}>
                <Grid item xs={12} md={8}>
                    <Box sx={{ marginTop: { xs: "0", md: "50px" }, position: "relative", width: "100%", display: "flex", flexDirection: "column", alignItems: "center" }}>
                        <Box sx={{ position: 'relative', width: { xs: "100%", md: "80%" }, height: { xs: "40vh", md: "70vh" } }}>
                            <video
                                style={{ position: "absolute", width: "100%", height: "100%" }}
                                preload='auto'
                                autoPlay
                                controls
                                src={video.video && `${video.video}`}
                            />
                        </Box>
                        <Typography sx={{ width: { xs: "100%", md: "80%" }, paddingLeft: { xs: "10px", md: "0" }, fontSize: "2rem", marginTop: "20px", color: "#ccc" }} >
                            {video.title}</Typography>
                        <Typography sx={{ width: "100%", paddingLeft: { xs: "10px", md: "100px", color: "#ccc" } }}>view's {video.length !== 0 && video.veiws.length}</Typography>
                        <Box sx={{ padding: { xs: "10px 0px 10px 10px", md: "20px 0" }, display: "flex", justifyContent: "space-between", width: { xs: "100%", md: "80%" }, alignItems: "center", columnGap: 2 }}>
                            <Box display={"flex"} sx={{ columnGap: { xs: 1, sm: 2 }, alignItems: "center" }}>
                                {
                                    owner.image ? <Box sx={{ width: "50px", height: "50px", position: "relative" }}>
                                        <img src={`${owner.image}`}
                                            style={{ width: "100%", height: "100%", objectFit: "cover", position: "absolute", borderRadius: "50%" }}
                                            alt={owner.image} />
                                    </Box> : <Avatar />
                                }
                                <Link to={`/dashboad/user/${owner._id}`}>
                                    <Typography sx={{ color: "#ccc" }}>{owner.name}</Typography>
                                    <Typography sx={{ color: "#ccc" }}>{owner.subcriber && owner.subcriber.length}</Typography>
                                </Link>
                                <form onSubmit={subcriberHandler}><Button type="submit" variant='outlined' sx={{ borderRadius: "20px", fontSize: { xs: "12px", md: "16px" } }}>{subcribe ? "UnSubcribe" : "Subcribe"}</Button></form>
                            </Box>
                            <Box display={"flex"} sx={{ columnGap: { xs: 2, sm: 3 }, alignItems: "center", paddingRight: "10px" }}>
                                <Box sx={{ display: "flex", justifyContent: "center", alignItems: "center", columnGap: 1, cursor: "pointer" }} onClick={likeHandler} >
                                    {like ? <ThumbUpRoundedIcon sx={{ color: "#ccc" }} /> : <ThumbUpAltOutlined sx={{ color: "#ccc" }} />}<Typography sx={{ color: "#ccc" }}>{likes}</Typography>
                                </Box>
                                <Box sx={{ display: "flex", justifyContent: "center", alignItems: "center", columnGap: 1, cursor: "pointer" }} onClick={UnlikeHandler}>
                                    {unlike ? <ThumbDownRounded sx={{ color: "#ccc" }} /> : <ThumbDownAltOutlined sx={{ color: "#ccc" }} />}<Typography sx={{ color: "#ccc" }}>{unlikes}</Typography>
                                </Box>
                            </Box>
                        </Box>
                        <Box sx={{ width: "80%" }}>
                            <Box sx={{ display: "flex", columnGap: 2 }}>
                                <Input
                                    sx={{ width: "100%", color: "#ccc" }}
                                    placeholder="Comments filter By Name's" />
                                <Button variant='outlined'>filter</Button>
                            </Box>
                        </Box>
                        <Box sx={{ width: "80%", marginTop: "20px" }} component={"form"} onSubmit={addCommentHandler}>
                            <Typography variant='h5'>Comment's</Typography>
                            <Box sx={{ display: "flex", columnGap: 2 }} >
                                <Input
                                    value={comment}
                                    sx={{ width: "100%", color: "#ccc" }}
                                    placeholder="Enter Your Comment's"
                                    onChange={inputHandler} />
                                <Button type='submit' variant='outlined'>comment</Button>
                            </Box>
                        </Box>
                        {
                            (typing.typing && typing.videoId === video._id) && <Box sx={{ display: "flex", padding: "0 5px", width: { xs: "100%", md: "80%" }, columnGap: { xs: 1, md: 2 }, margin: "20px 0" }}>
                                <Avatar
                                    src={typing.profile && `${typing.profile}`} />
                                <Box>
                                    <Typography sx={{ color: "#ccc" }}>{typing.name}</Typography>
                                    <Typography sx={{ color: "#ccc" }}>Typing...</Typography>
                                </Box>
                            </Box>
                        }
                        {
                            comments.map((elm, ind) => {
                                return (
                                    <Box key={ind} sx={{ display: "flex", padding: "0 5px", width: { xs: "100%", md: "80%" }, columnGap: { xs: 1, md: 2 }, margin: "20px 0" }}>
                                        {
                                            elm.profile ? <Box sx={{ width: "50px", height: "50px", position: "relative" }}>
                                                <img src={`${elm.profile}`}
                                                    style={{ width: "100%", height: "100%", objectFit: "cover", position: "absolute", borderRadius: "50%" }}
                                                    alt={elm.profile} />
                                            </Box> : <Avatar />
                                        }
                                        <Box>
                                            <Typography sx={{ color: "#ccc" }}>{elm.user}</Typography>
                                            <Typography sx={{ color: "#ccc" }}>
                                                {elm.comment}
                                            </Typography>
                                        </Box>
                                    </Box>
                                )
                            })
                        }
                    </Box>
                </Grid>
                <Grid item xs={4}>
                    <Box sx={{ width: "100%", height: "100%", display: { xs: "none", md: "block" } }}>
                        {
                            [...Array(10).keys()].map((elm, ind) => {
                                return (<ListItem key={ind}>
                                    <Link to={`/dashboad/videos/${video._id}`} style={{ display: "flex" }}>
                                        <Typography sx={{ height: "25px", margin: "auto 10px auto 0px", color: "#ccc" }}>{ind + 1}</Typography>
                                        <Box className={'ListVideoContainer'} sx={{ width: "200px", height: "15vh", color: "#ccc" }}>
                                            <video
                                                style={{ position: "absolute", width: "100%", height: "100%" }}
                                                preload='auto'
                                                muted
                                                src={video.video && `${video.video}`}
                                            />
                                        </Box>
                                        <Box>
                                            <Typography variant='h5' sx={{ width: { xs: "100%" }, ml: 2, fontSize: { xs: "14px", sm: "20px", color: "#ccc" } }} >
                                                {video.title}
                                            </Typography>
                                            <Typography sx={{ ml: { xs: 2 }, marginTop: "11px", color: "#ccc" }}>subcription title 1 month</Typography>
                                            <Divider />
                                        </Box>
                                    </Link>
                                </ListItem>)
                            })
                        }
                    </Box>
                </Grid>
            </Grid>}
            {
                (!loading && error) && <Refuse />
            }
            <Snackbar open={open} onClose={() => setOpen(false)} autoHideDuration={3000}>
                <Alert onClose={() => setOpen(false)} variant='filled' severity={message.success ? "success" : 'error'}>
                    {message.message}
                </Alert>
            </Snackbar>
        </>
    )
}

export default PlayVideo