import React, { useEffect, useState, useContext } from 'react';
import { NavLink } from 'react-router-dom'
import SubscriptionsRoundedIcon from '@mui/icons-material/SubscriptionsRounded';
import ThumbUpRoundedIcon from '@mui/icons-material/ThumbUpRounded';
import RestoreRoundedIcon from '@mui/icons-material/RestoreRounded';
import logo from '../assets/background/logo.jpg'
import VideoLibraryRoundedIcon from '@mui/icons-material/VideoLibraryRounded';
import WatchLaterRoundedIcon from '@mui/icons-material/WatchLaterRounded';
import NotificationsIcon from '@mui/icons-material/Notifications';
import HomeRoundedIcon from '@mui/icons-material/HomeRounded';
import { DataContext } from '../context/drawer';
import { Box, List, ListItem, ListItemIcon, ListItemText, Drawer, Badge, Divider, Avatar } from "@mui/material"
import { AddAPhoto } from '@mui/icons-material';
import { NotificationContext } from '../context/NotificationContext';
import { SearchContext } from '../context/search';


const ListContainer = () => {
  const { myaccount } = useContext(SearchContext)
  const { notificationIcon, setNotificationIcon } = useContext(NotificationContext)
  const { DrawerOpen, setDrawerOpen } = useContext(DataContext)
  const [active, setActive] = useState(window.location.pathname)
  const list = [
    {
      listName: "Home",
      link: "/dashboad/home",
      icon: <HomeRoundedIcon sx={{ color: ("/dashboad/home" !== active) ? "#ccc" : "" }} />,
    },
    {
      listName: "Subcription",
      link: "/dashboad/subcription",
      icon: <SubscriptionsRoundedIcon sx={{ color: ("/dashboad/subcription" !== active) ? "#ccc" : "" }} />,
    },
    {
      listName: "Library",
      link: "/dashboad/library",
      icon: <VideoLibraryRoundedIcon sx={{ color: ("/dashboad/library" !== active) ? "#ccc" : "" }} />,
    },
    {
      listName: "History",
      link: "/dashboad/history",
      icon: <RestoreRoundedIcon sx={{ color: ("/dashboad/history" !== active) ? "#ccc" : "" }} />,
    },
    {
      listName: "Watch Later",
      link: "/dashboad/watchlater",
      icon: <WatchLaterRoundedIcon sx={{ color: ("/dashboad/watchlater" !== active) ? "#ccc" : "" }} />,
    },
    {
      listName: "Like Videos",
      link: "/dashboad/like",
      icon: <ThumbUpRoundedIcon sx={{ color: ("/dashboad/like" !== active) ? "#ccc" : "" }} />,
    },
    {
      listName: "Add Video",
      link: "/dashboad/addvideo",
      icon: <AddAPhoto sx={{ color: ("/dashboad/addvideo" !== active) ? "#ccc" : "" }} />,
    },
    {
      listName: "My Acount",
      link: "/dashboad/myacount",
      icon: <Avatar
        sx={{ width: "1.7rem", height: "1.7rem", color: ("/dashboad/myacount" !== active) ? "#ccc" : "" }}
        src={myaccount.image ? `${myaccount.image}` : ""} />
    },
  ]


  useEffect(() => {
    setActive(window.location.pathname)
    document.body.style.backgroundColor = "black"
  }, [])



  return (

    <>
      <Box sx={{ width: "16.5vw", borderRight: "1px solid lightGray", height: "100vh", overflowY: "hidden" }}>
        <List sx={{ marginTop: "59px", display: { xs: "none", md: "block" } }} >
          {
            list.map((elm, ind) => {
              return (
                <ListItem key={ind} className={elm.link === active ? 'listActiveDarkMode' : ""}>
                  <ListItemIcon >
                    {elm.icon}
                  </ListItemIcon>
                  <NavLink to={elm.link} style={{ color: elm.link !== active ? "#ccc" : "" }} className={(elm.link === active) ? 'listActiveDarkMode' : ""}>
                    <ListItemText
                      primary={elm.listName}
                    />
                  </NavLink>
                </ListItem>
              )
            })
          }
          <ListItem onClick={() => setNotificationIcon(null)} className={("/dashboad/notification" === active) ? "listActiveDarkMode" : ""} sx={{ color: ("/dashboad/notification" === active) ? "#ccc" : "black" }}>
            <ListItemIcon>
              <Badge badgeContent={notificationIcon} color="error">
                <NotificationsIcon sx={{ color: ("/dashboad/notification" !== active) ? "#ccc" : "" }} />
              </Badge>
            </ListItemIcon>
            <NavLink to={"/dashboad/notification"} style={{ color: ("/dashboad/notification" !== active) ? "#ccc" : "black" }}>
              <ListItemText
                primary={"Notification"}
              />
            </NavLink>
          </ListItem>
        </List>
      </Box>
      <Box
        sx={{ flexGrow: 1, bgcolor: 'background.paper', display: 'flex', height: "100vh", width: "100%" }}
      >
        <Drawer

          PaperProps={{
            sx: { bgcolor: "black" }
          }}
          anchor={"left"}
          open={DrawerOpen}
          onClose={() => setDrawerOpen(false)}
        >
          <Box
            sx={{ width: 250 }}
            role="presentation"
            onClick={() => setDrawerOpen(false)}
          >
            <img src={logo} alt="logo" style={{ width: "200px", marginLeft: 25, height: "15vh", objectFit: "cover" }} />
            <Divider sx={{ margin: "10px 0", bgcolor: "#ccc" }} />
            <List >
              {
                list.map((elm, ind) => {
                  return (
                    <ListItem key={ind} className={elm.link === active ? 'listActiveDarkMode' : ""}>
                      <ListItemIcon >
                        {elm.icon}
                      </ListItemIcon>
                      <NavLink to={elm.link} style={{ color: elm.link !== active ? "#ccc" : "" }} className={(elm.link === active) ? 'listActiveDarkMode' : ""}>
                        <ListItemText
                          primary={elm.listName}
                        />
                      </NavLink>
                    </ListItem>
                  )
                })
              }
              <ListItem onClick={() => setNotificationIcon(null)} className={("/dashboad/notification" === active) ? "listActiveDarkMode" : ""} sx={{ color: ("/dashboad/notification" === active) ? "#ccc" : "black" }}>
                <ListItemIcon>
                  <Badge badgeContent={notificationIcon} color="error">
                    <NotificationsIcon sx={{ color: ("/dashboad/notification" !== active) ? "#ccc" : "" }} />
                  </Badge>
                </ListItemIcon>
                <NavLink to={"/dashboad/notification"} style={{ color: ("/dashboad/notification" !== active) ? "#ccc" : "black" }}>
                  <ListItemText
                    primary={"Notification"}
                  />
                </NavLink>
              </ListItem>
            </List>
            <address className={"drawerFooter"}>
              <Divider sx={{ bgcolor: "#ccc" }} />
              <Box sx={{ width: "100%", textAlign: "center" }}><i style={{ color: "#ccc", textAlign: "center" }}>created By sageer ansari </i></Box>
            </address>
          </Box>
        </Drawer>
      </Box>
    </>
  )
}

export default ListContainer