import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
import { Avatar, Box, CardMedia, Grid } from '@mui/material';
import { Link } from 'react-router-dom';
import Axios from '../Axios';
import { useState } from 'react';
import Appbar from '../components/Appbar';
import ListContainer from '../components/List';
import Loading from '../components/Loading';
import Refuse from '../components/Refuse';
import NoList from '../components/NoList';


const Subcription = () => {
  const [loading, setLoading] = useState(false)
  const [subcription, setSubcrptions] = useState([])
  const [message, setMessage] = useState(null)
  const fetchSubcription = async () => {
    try {
      setLoading(true)
      const { data } = await Axios.get("/getsubcription")
      setLoading(false)
      setSubcrptions(data.subcriptions)
    } catch (error) {
      setLoading(false)
      setMessage(error.message)
    }
  }
  React.useEffect(() => {
    fetchSubcription()
  }, [])
  return (
    <>
      <Appbar />
      <Grid container>
        <Grid item xs={2} sx={{ display: { xs: "none", md: "block" } }}>
          <Box sx={{ width: "16.5vw", borderRight: "1px solid lightGray", height: "90vh", position: "fixed" }}>
            <ListContainer />
          </Box>
        </Grid>
        <Grid item xs={12} md={10} marginTop={"70px"}>
          {loading ? <Loading /> : <Grid container gap={3} sx={{ display: 'flex', justifyContent: "center", alignItems: "center" }}>
            {
              subcription.map((elm, ind) => {
                return (
                  <Grid key={ind}>
                    <Card sx={{ maxWidth: 345, maxHeight: 320, bgcolor: "black", boxShadow: `2px 1px 5px 1px #ccc` }}>
                      <CardMedia
                        sx={{ height: 200 }}
                        image={`${elm.image}`}
                        title={elm.name}
                      />
                      <CardContent>
                        <Link to={`/dashboad/user/${elm._id}`} style={{ display: "flex", color: "#ccc", columnGap: "10px", alignItems: "center", height: "50px" }}>
                          {
                            "video.OwenerProfile" ? <Box sx={{ width: "50px", height: "50px", position: "relative" }}>
                              <img src={`${elm.image}`}
                                style={{ width: "100%", height: "100%", objectFit: "cover", position: "absolute", borderRadius: "50%" }}
                                alt={"video.OwenerProfile"} />
                            </Box> : <Avatar />
                          }
                          <Box>
                            <Typography variant="h5" component="div" sx={{ width: "250px" }}>
                              {elm.name}
                            </Typography>
                            <Typography variant="h5" component="div" sx={{ width: "250px" }}>
                              {elm.email}
                            </Typography>
                          </Box>
                        </Link>
                      </CardContent>
                      <CardActions sx={{ display: "flex", justifyContent: "space-between", alignItems: "center", marginTop: -1 }}>
                        <Typography variant="body2" color="text.secondary" sx={{ color: "#ccc" }}>{elm.subcriber.length} subcribers</Typography>
                      </CardActions>
                    </Card>
                  </Grid>
                )
              })
            }
          </Grid>}
          {
            (!loading && message) && <Refuse />
          }
          {
            (subcription.length === 0 && !loading) && <NoList />
          }
        </Grid>
      </Grid>
    </>
  )
}

export default Subcription