import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import Home from './pages/Home';
import "./assets/style/app.scss";
import DrawerProvider from './context/drawer';
import Subcription from './pages/Subcription';
import WatchLater from './pages/WatchLater';
import Like from './pages/Like';
import Notification from './pages/Notification';
import Library from './pages/Library';
import VideoHistory from './pages/History';
import PlayVideo from './pages/PlayVideos';
import SignUp from './pages/SignUp';
import Cookies from 'universal-cookie'
import Login from './pages/Login';
import MyAcount from './pages/MyAcount';
import AddVideo from './pages/AddVideo';
import NotificationProvider from './context/NotificationContext';
import SearchProvider from './context/search';
import { useEffect } from 'react';
import UserProfile from './pages/UserProfile';
import { ThemeProvider, createTheme } from '@mui/material';
import Error from './pages/Error';


const theme = createTheme({
  palette: {
    primary: {
      main: '#f00', // specify the default color
    },
  },
  overrides: {
    MuiOutlinedInput: {
      focused: {
        borderColor: '#0f0', // specify the focus color
      },
    },
  },
});

function App() {
  const cookie = new Cookies()
  const token = cookie.get("token")

  useEffect(() => {
    document.body.style.backgroundColor = "black"
  })
  return (
    <Router>
      <ThemeProvider theme={theme}>
        <SearchProvider>
          <NotificationProvider>
            <DrawerProvider>
              <Routes>
                <Route path='/dashboad/home' element={token ? <Home /> : <Login />} />
                <Route path='/dashboad/addvideo' element={token ? <AddVideo /> : <Login />} />
                <Route path='/dashboad/subcription' element={token ? <Subcription /> : <Login />} />
                <Route path='/dashboad/history' element={token ? <VideoHistory /> : <Login />} />
                <Route path='/dashboad/watchlater' element={token ? <WatchLater /> : <Login />} />
                <Route path='/dashboad/like' element={token ? <Like /> : <Login />} />
                <Route path='/dashboad/notification' element={token ? <Notification /> : <Login />} />
                <Route path='/dashboad/myacount' element={token ? <MyAcount /> : <Login />} />
                <Route path='/dashboad/library' element={token ? <Library /> : <Login />} />
                <Route path='/dashboad/videos/:id' element={token ? <PlayVideo /> : <Login />} />
                <Route path='/dashboad/user/:id' element={token ? <UserProfile /> : <Login />} />
                <Route path='/sign-up' element={<SignUp />} />
                <Route path='/login' element={<Login />} />
                <Route path='*' element={<Error />} />
              </Routes>
            </DrawerProvider>
          </NotificationProvider>
        </SearchProvider>
      </ThemeProvider>
    </Router>
  )
}


export default App
