import React, { useEffect, useState } from 'react';
import ListContainer from '../components/List'
import { Box, Grid } from '@mui/material'
import Appbar from '../components/Appbar';
import Components from '../components/Components';
import Axios from '../Axios';
import { ThumbDownRounded } from "@mui/icons-material";
import Loading from '../components/Loading';
import Refuse from '../components/Refuse';



const Like = () => {
  const [loading, setLoading] = useState(false)
  const [likevideo, setLikevideo] = useState([])
  const [message, setMessage] = useState("")


  const fetchLikevideo = async () => {
    try {
      setLoading(true)
      const { data } = await Axios.get("/showlikevideo")
      setLoading(false)
      setLikevideo(data.showLikeVideo)
    } catch (error) {
      setLoading(false)
      setMessage(error.response.data || error.message)
    }
  }
  useEffect(() => {
    fetchLikevideo()
  }, [])
  return (
    <>
      <Appbar />
      <Grid container>
        <Grid item xs={2} sx={{ display: { xs: "none", md: "block" } }}>
          <Box sx={{ width: "16.5vw", borderRight: "1px solid lightGray", height: "90vh", position: "fixed" }}>
            <ListContainer />
          </Box>
        </Grid>
        <Grid item xs={12} md={10}>
          <Grid container  >
            {
              loading ? <Loading /> : <Components title={"Like Videos"} video={likevideo} icon={<ThumbDownRounded />}
                fetchdata={fetchLikevideo} option={"Unlike"} loading={loading}/>
            }
            {
              (!loading && message) && <Refuse />
            }
            
          </Grid>
        </Grid>
      </Grid>
    </>
  )
}

export default Like