import { Avatar, Box, Button, Divider, Grid, Typography } from '@mui/material'
import React, { useEffect, useState } from 'react'
import Appbar from '../components/Appbar'
import ListContainer from '../components/List';
import SearchIcon from '@mui/icons-material/Search';
import { styled, alpha } from '@mui/material/styles';
import { useParams } from 'react-router-dom';
import InputBase from '@mui/material/InputBase';
import Axios from '../Axios';
import Video from '../components/Videos';
import Loading from '../components/Loading';


const Search = styled('div')(({ theme }) => ({
    position: 'relative',
    borderRadius: theme.shape.borderRadius,
    backgroundColor: alpha(theme.palette.common.white, 0.15),
    '&:hover': {
        backgroundColor: alpha(theme.palette.common.white, 0.25),
    },
    marginRight: theme.spacing(2),
    marginLeft: 0,
    width: '100%',
    [theme.breakpoints.up('sm')]: {
        marginLeft: theme.spacing(3),
        width: 'auto',
    },
}));


const SearchIconWrapper = styled('div')(({ theme }) => ({
    padding: theme.spacing(0, 2),
    height: '100%',
    position: 'absolute',
    pointerEvents: 'none',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
}));


const StyledInputBase = styled(InputBase)(({ theme }) => ({
    color: 'inherit',
    '& .MuiInputBase-input': {
        padding: theme.spacing(1, 1, 1, 0),
        // vertical padding + font size from searchIcon
        paddingLeft: `calc(1em + ${theme.spacing(6)})`,
        transition: theme.transitions.create('width'),
        width: '100%',
        [theme.breakpoints.up('md')]: {
            width: '50ch',
        },
    },
}));


const UserProfile = () => {
    const [user, setUser] = useState({})
    const [videos, setVideos] = useState([])
    const [loading, setLoading] = useState(false)
    const { id } = useParams()


    useEffect(() => {
        const fetchProfile = async () => {
            try {
                setLoading(true)
                const { data: { user, userVideos } } = await Axios.get(`/showuser/${id}`)
                setLoading(false)
                setUser(user)
                setVideos(userVideos)
            } catch (error) {
                setLoading(false)
            }
        }
        fetchProfile()
    }, [id])


    return (

        <>
            <Appbar />
            <Grid container>
                <Grid item xs={2} sx={{ display: { xs: "none", md: "block" } }}>
                    <Box sx={{ width: "16.5vw", borderRight: "1px solid lightGray", height: "90vh", position: "fixed" }}>
                        <ListContainer />
                    </Box>
                </Grid>
                <Grid item xs={12} md={10}>
                    {loading ? <Loading /> : <Box sx={{
                        marginTop: "70px", width: "100%", height: "100%"
                        , gap: 3, flexDirection: "column", display: "flex", justifyContent: "center", alignItems: "center"
                    }}>
                        <Avatar src={user.image ? `${user.image}` : ""}
                            sx={{ width: "5rem", height: "5rem" }} />
                        <Box textAlign={"center"} sx={{ width: "100%" }}>
                            <Typography variant='h4' sx={{ fontWeight: "500", marginBottom: "10px", color: "#ccc" }}>{user.name}</Typography>
                            <Typography variant='h5' sx={{ fontWeight: "500", marginBottom: "10px", color: "#ccc" }}>{user.email}</Typography>
                            <Button variant='outlined' sx={{ margin: "10px 0" }}>subcribers</Button>
                            <Typography variant='outlined' sx={{ margin: "10px 0px 10px 10px", color: "#ccc" }}>{user.subcriber && user.subcriber.length}</Typography>
                            <Divider sx={{ bgcolor: "#ccc" }} />
                        </Box>
                        <Box>
                            <Box display={"flex"} justifyContent={"center"} alignItems={"center"} sx={{ margin: "10px 0" }}>
                                <Search>
                                    <SearchIconWrapper>
                                        <SearchIcon sx={{ color: "#ccc" }} />
                                    </SearchIconWrapper>
                                    <StyledInputBase
                                        placeholder="Search"
                                        sx={{ color: "#ccc" }}
                                        inputProps={{ 'aria-label': 'search' }}
                                    />
                                </Search>
                            </Box>
                            <Grid container display={"flex"} gap={3} justifyContent={"center"} alignItems={"center"}>
                                {
                                    videos.map((video, ind) => {
                                        return (<Grid item key={ind}>
                                            <Video video={video} />
                                        </Grid>)
                                    })
                                }
                            </Grid>
                        </Box>
                    </Box>}
                </Grid>
            </Grid>
        </>
    )
}

export default UserProfile
// import { useParams } from 'react-router-dom'

