import { Box, Button, Typography } from '@mui/material'
import React from 'react'

const Refuse = () => {
    return (
        <Box sx={{ color: "#ccc", display: "flex", justifyContent: "center", width: "100%", height: "90vh", alignItems: "center" }}>
            <Box>
                <Typography sx={{ textAlign: "center" }}>something wen't wrong reload the page <br /> or Try again some Time</Typography>
                <Button onClick={() => window.location.reload()} sx={{ backgroundColor: "red", color: "#ccc", width: "100%", marginTop: "20px" }}>reload page</Button>
            </Box>
        </Box>
    )
}

export default Refuse