import { Alert, Box, Button, Divider, Grid, List, ListItem, Menu, MenuItem, Snackbar, Typography } from "@mui/material"
import React, { useState } from "react";
import me from '../assets/img/me.jpg';
import ShuffleIcon from '@mui/icons-material/Shuffle';
import PlayCircleIcon from '@mui/icons-material/PlayCircle';
import { styled, alpha } from '@mui/material/styles';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { Link } from "react-router-dom";
import Axios from "../Axios";




const StyledMenu = styled((props) => (
  <Menu
    elevation={0}
    anchorOrigin={{
      vertical: 'bottom',
      horizontal: 'right',
    }}
    transformOrigin={{
      vertical: 'top',
      horizontal: 'right',
    }}
    {...props}
  />
))(({ theme }) => ({
  '& .MuiPaper-root': {
    borderRadius: 6,
    marginTop: theme.spacing(1),
    minWidth: 180,
    color:
      theme.palette.mode === 'light' ? 'rgb(55, 65, 81)' : theme.palette.grey[300],
    boxShadow:
      'rgb(255, 255, 255) 0px 0px 0px 0px, rgba(0, 0, 0, 0.05) 0px 0px 0px 1px, rgba(0, 0, 0, 0.1) 0px 10px 15px -3px, rgba(0, 0, 0, 0.05) 0px 4px 6px -2px',
    '& .MuiMenu-list': {
      padding: '4px 0',
    },
    '& .MuiMenuItem-root': {
      '& .MuiSvgIcon-root': {
        fontSize: 18,
        color: theme.palette.text.secondary,
        marginRight: theme.spacing(1.5),
      },
      '&:active': {
        backgroundColor: alpha(
          theme.palette.primary.main,
          theme.palette.action.selectedOpacity,
        ),
      },
    },
  },
}));


const Components = ({ title, video, option, icon, fetchdata, loading }) => {
  const [anchorEl, setAnchorEl] = useState(null);
  const [opens, setOpens] = useState(false)
  const [message, setMessage] = useState([])
  const open = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };

  const unlikeVideo = async (id) => {
    try {
      if (option === "Unlike") {
        const { data } = await Axios.post(`/likevideo`, { id })
        setMessage(data)
        setOpens(true)
        setAnchorEl(false)
        fetchdata()
      }
      if (option === "Remove Watch Later") {
        const { data } = await Axios.put(`/unsavewatchlater/${id}`)
        setMessage(data)
        setOpens(true)
        setAnchorEl(false)
        fetchdata()
      }
    } catch (error) {
      setMessage(error.response.data)
      setOpens(true)
    }
  }

  return (
    <>
      <Grid item xs={4} sx={{ display: { xs: "none", md: "block" } }}>
        <Box className={"videoBanner"} sx={{ width: "100%", height: "100vh" }} >
          <Box sx={{ display: "flex", justifyContent: "center", flexDirection: "column" }}>
            <Box className={"imageContainer"}><img src={me} alt="this is me" /></Box>
            <Typography variant={"h4"} sx={{ fontWeight: "600", marginTop: "20px", color: "#ccc" }}>{title}</Typography>
            <Typography variant={"secondary-body2"} sx={{ marginTop: { xs: "10px", sm: "20px", color: "#ccc" } }} >{video.length !== 0 ? video[0].title : "Title"}</Typography>
            <Link to={`/dashboad/videos/${video.length !== 0 ? video[0]._id : ""}`}><Button variant='outlined' sx={{ marginTop: "20px" }} startIcon={<PlayCircleIcon />}>Play all</Button>
              <Button variant='outlined' sx={{ marginTop: "20px", marginLeft: "10px" }} startIcon={<ShuffleIcon />}>Shuffle</Button></Link>
          </Box>
        </Box>
      </Grid>
      <Grid item sx={{ width: { xs: "100%", md: "66.6%" } }}>
        <List sx={{ width: "100%", height: "90vh", overflowY: "scroll", marginTop: "59px" }}>
          {
            (video.length === 0 && loading === false) ? <Box sx={{ width: "100%", height: "85vh", color: "#ccc", display: "grid", placeItems: "center" }}><Typography variant="h3">No Videos</Typography></Box> : video.map((elm, ind) => {
              return (
                <ListItem key={ind} >
                  <Link to={`/dashboad/videos/${elm._id}`} style={{ display: "flex", width: "100%" }} >
                    <Typography sx={{ height: "25px", margin: "auto 10px auto 5px", color: "#ccc" }}>{ind + 1}</Typography>
                    <Box className={'ListVideoContainer'} sx={{ width: "300px", height: { xs: "13vh", sm: "18vh" } }}><video
                      preload='auto'
                      muted
                      src={`${elm.video}`} /></Box>
                    <Box width={"80%"}>
                      <Typography variant='h5' sx={{ width: { xs: "210px", sm: "300px", md: "80%" }, height: { xs: "55%", md: "53%" }, ml: 2, fontSize: { xs: "18px", sm: "20px", md: "25px", color: "#ccc" } }} >
                        {elm.title.length > 48 ? `${elm.title.slice(0, 48)}...` : elm.title}
                      </Typography>
                      <Typography sx={{ ml: { xs: 2, md: 2 }, mt: { xs: 1, sm: 2, md: 3 }, mb: { xs: 1, sm: 2, md: 3 }, color: "#ccc" }}>{elm.veiws.length} veiw's 1 month ago</Typography>
                      <Divider />
                    </Box>
                  </Link>
                  <Box>
                    <MoreVertIcon onClick={handleClick} />
                    <StyledMenu
                      id="demo-customized-menu"
                      MenuListProps={{
                        'aria-labelledby': 'demo-customized-button',
                      }}
                      anchorEl={anchorEl}
                      open={open}
                      onClose={handleClose}
                    >
                      <MenuItem onClick={() => unlikeVideo(elm._id)} disableRipple>
                        {icon}
                        <Typography>{option}</Typography>
                      </MenuItem>
                    </StyledMenu>
                  </Box>
                </ListItem>
              )
            })
          }
        </List>
      </Grid>
      <Snackbar autoHideDuration={3000} open={opens} onClose={() => setOpens(false)}>
        <Alert onClose={() => setOpens(false)} variant='filled' severity={message.success ? "success" : "error"}>
          {message.message}
        </Alert>
      </Snackbar>
    </>
  )
}

export default Components